.modal {
  position: absolute;
  top: calc((110% - 400px) / 2);
  left: calc((100% - 450px) / 2);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  height: 400px;
  width: 450px;
  padding: 10px;
  background: aliceblue;
  border-radius: 10px;
  transition: 0.3s transform;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.modal.-hide {
  transform: translateY(100vh);
}
.modal .item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 95px;
  width: 95px;
  background: cornflowerblue url(src/components/modal-dialog/img/diploma.svg) no-repeat;
  background-size: 90%;
  background-position: center center;
  border-radius: 10px;
  color: azure;
  text-shadow: 1px 1px black;
  transition: 0.3s background, 0.3s color;
  cursor: pointer;
}
.modal .item:hover,
.modal .item.-selected {
  background: #6a3a1a url(src/components/modal-dialog/img/diploma.svg) no-repeat;
  background-size: 90%;
  background-position: center center;
  color: blanchedalmond;
}
.modal .btn-close {
  position: absolute;
  right: -10px;
  top: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20px;
  width: 20px;
  background: red;
  border: 1px solid black;
  font-family: "Arial", "Tahoma", sans-serif;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s ease-in background;
}
.modal .btn-close:hover {
  background: #a10000;
}