@keyframes pain {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-20px);
  }
}
.animation.thunder {
  top: -250px;
  left: -20px;
}
.animation.water {
  top: -220px;
  left: -125px;
}
.animation.fire {
  top: -220px;
  left: -80px;
}
.animation.explosion {
  top: -150px;
  left: -185px;
}
.animation.thunder,
.animation.water,
.animation.fire,
.animation.explosion {
  position: absolute;
  z-index: 25;
  max-height: 400px;
  filter: blur(1px);
}
.-pain {
  animation-name: pain;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
}