body > .score-component {
  position: absolute;
  top: calc((100% - 350px) / 2);
  left: calc((100% - 250px) / 2);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 350px;
  width: 250px;
  background: aliceblue url(src/screens/score/img/like.svg) no-repeat;
  background-size: contain;
  background-blend-mode: luminosity;
  border-radius: 10px;
  transition: 1s opacity;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
body > .score-component.-hide {
  opacity: 0;
}
body > .score-component .title {
  padding: 10px;
  color: brown;
  font-size: 20px;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: 15px;
  border-bottom: 1px solid black;
  text-shadow: 1px 1px 2px darkorange;
}
body > .score-component .score-table {
  padding: 10px;
}
body > .score-component .score-table li {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  padding: 5px 10px;
  background: rgba(100, 100, 100, 0.3);
}
body > .score-component .score-table li span {
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 18px;
}
body > .score-component .btn-new-game {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 30px;
  background-color: aquamarine;
  border-radius: 5px;
  cursor: pointer;
}