@-moz-keyframes spin {
  0% {
    -moz-transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes appears {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  75% {
    opacity: 0.7;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.waiting {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: black;
  transition: 1s ease-in-out all;
}
.waiting.-loaded {
  transform: rotate(-45deg);
  top: 25vh;
  left: -45vw;
  height: 100px;
  width: 200vw;
}
.waiting.-hide {
  height: 0;
}
.waiting .circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 220px;
  border: 10px solid rgba(0, 183, 229, 0.9);
  border-radius: 50%;
  border-bottom-color: transparent;
  border-top-color: transparent;
  box-shadow: 0 0 60px #2187e7;
  -moz-animation: spin 6s infinite linear;
  -webkit-animation: spin 6s infinite linear;
}
.waiting .circle.-hide {
  transition: 1s opacity;
  opacity: 0;
}
.waiting .circle > .circle {
  width: 150px;
  height: 150px;
  font-size: 32px;
  color: wheat;
  border: 10px solid rgba(229, 203, 0, 0.9);
  border-left-color: transparent;
  border-right-color: transparent;
  box-shadow: 0 0 35px #2187e7;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -moz-animation: spin reverse 4s infinite linear;
  -webkit-animation: spin reverse 4s infinite linear;
}
.entry-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: 0.5s opacity;
}
.entry-screen.-hide {
  opacity: 0;
}
.entry-screen .logo {
  max-width: 500px;
  object-fit: contain;
  transition: 1s opacity;
}
.entry-screen .logo.-hide {
  opacity: 0;
}
.entry-screen .btn-enter {
  margin-top: 30px;
  font-size: 46px;
  opacity: 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  animation: appears 2s 1s infinite linear;
  transition: 1s opacity;
}