.modal > p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-family: "Arial", "Tahoma", sans-serif;
}
.modal > p:first-child {
  text-transform: uppercase;
}
.modal > p:nth-child(3) {
  font-weight: 600;
}
.modal > input {
  width: 100%;
  height: 50px;
  font-family: "Tahoma", "Arial", sans-serif;
  text-align: center;
}
.modal > .btn-done {
  height: 35px;
  background: cadetblue;
  font-weight: 600;
  border: 1px solid wheat;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s background, 0.3s color;
}
.modal > .btn-done:hover {
  background: #68d9dc;
  color: #495749;
}